import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import web_001 from '../../images/works_web_001.png'


export default function ReposityDisplay() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='reposityDisplay'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <div>GitHub查詢</div>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>製作日期:2022/3-4月</p></li>
                                    <li><p>side project</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>這是一個可以搜尋GitHub用戶，然後將用戶的reposity展示的網站，我在這個專案中學會了如何使用react進行前端開發，以及api的用法</h4>
                                <h4>1. 網站連結:<br/><a href='https://xiaoxuan0117.github.io/reposity_display/' target='_blank' rel="noreferrer">https://xiaoxuan0117.github.io/reposity_display/</a></h4>
                                <h4>2. 到GitHub查看原始碼:<br/><a href='https://github.com/Xiaoxuan0117/reposity_display' target='_blank' rel="noreferrer">https://github.com/Xiaoxuan0117/reposity_display</a></h4>
                                <h4>3. HackMD上有簡單解釋:<br/><a href='https://reurl.cc/OpOZ99' target='_blank' rel="noreferrer">https://reurl.cc/OpOZ99</a></h4>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery" data-src={web_001}>
                                        <div className="img" style={{backgroundImage: `url(${web_001})`}} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
