import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import '@fancyapps/ui'
import "@fancyapps/ui/dist/fancybox.css";
import mainPage from './images/mainPage.png'
import singleArticle from './images/singleArticle.png'
import login from './images/login.png'
import register from './images/register.png'
import newestArticle from './images/newestArticle.png'
import article1 from './images/article1.png'
import article2 from './images/article2.png'

export default function Enterprise() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='enterprise'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <a href="https://github.com/Xiaoxuan0117/enterprise_website">企業地圖網站<span>link to github</span></a>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>製作日期:2021年4-5月</p></li>
                                    <li><p>課程:資料庫管理</p></li>
                                    <li><p>類型:小組報告</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>資料庫管理是工資系的必修課，主要是教大家<span>MySQL</span>的語法，像是怎麼定義資料(Primary key, Foreign key...)，還有怎麼篩選資料等等。

                                </h4>
                                <h4>在這堂課一個很重要的成果就是要製作能夠傳輸、儲存資料的網站，我們這組選到的網站類型是叫做<span>企業地圖</span>，參考的網站是"求職天眼通"。
                                </h4>
                                <h4>企業地圖網站提供給所有人一個平台去<span>分享自己在不同企業面試以及工作的心得</span>，會像一篇一篇文章一樣被發表，大家也可以去文章下面<span>留言</span>，還有一個很重要的功能是可以<span>註冊帳號</span>與<span>登入</span>、<span>登出</span>。
                                </h4>
                                <h4>我們依照助教提出的功能要求做出了這樣的一個網頁，但是每次執行都需要開虛擬主機，所以都是自己在demo，沒有真的讓陌生人使用，不過經過這次資料庫的經驗，讓我更加的對於這種網頁相關的領域越來越有興趣！
                                </h4>
                                <h4>接下來介紹幾個網頁的功能和架構：<br/>
                                這邊是進入的主畫面，使用者可以輸入關鍵字來搜尋文章。
                                </h4>
                                <h5>#主畫面</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery1" data-src={mainPage}>
                                        <div className="img" style={{backgroundImage: `url(${mainPage})`}}></div>
                                    </div>
                                </div>
                                <h5>#首頁文章:首頁會放上最新文章</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery5" data-src={newestArticle}>
                                        <div className="img" style={{backgroundImage: `url(${newestArticle})`}}></div>
                                    </div>
                                </div>
                                <h4>搜尋到文章之後可以點入文章查看，並且留言。</h4>
                                <h5>#留言區</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery2" data-src={singleArticle}>
                                        <div className="img" style={{backgroundImage: `url(${singleArticle})`}}></div>
                                    </div>
                                </div>
                                <h4>但是在留言之前要先登入或註冊。</h4>
                                <h5>#登入畫面</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery3" data-src={login}>
                                        <div className="img" style={{backgroundImage: `url(${login})`}}></div>
                                    </div>
                                </div>
                                <h5>#註冊畫面</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery4" data-src={register}>
                                        <div className="img" style={{backgroundImage: `url(${register})`}}></div>
                                    </div>
                                </div>
                                <h4>登入帳號之後除了可以留言還可以發表文章。</h4>
                                <h5>#發表文章頁面</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery5" data-src={article1}>
                                        <div className="img" style={{backgroundImage: `url(${article1})`}}></div>
                                    </div>
                                </div>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery5" data-src={article2}>
                                        <div className="img" style={{backgroundImage: `url(${article2})`}}></div>
                                    </div>
                                </div>
                                <h4>企業地圖的介紹大致上就到這裡，github有原始碼可以查看。</h4>
                                <h4>當時製作的時候對於網頁很多內容都還不是很熟悉，也花了蠻多時間製作，但看到有完成結果所以覺得是很有趣的經驗！</h4>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
