import React from 'react'
import about_photo from '../../images/about_photo.png'

export default function SideInfo() {
  return (
        <div className="col-lg-3 d-none d-lg-block">
            <div id="side_info">
                <div className="photoBox">
                    <div className="photo_inner">
                        <div className="photo" style={{backgroundImage: `url(${about_photo})`}}></div>
                    </div>
                </div>
                <div className="brief_info color-grey">
                    <div className="name color-secondary">
                        <h4>潘小璇</h4>
                        <h4>XIAO XUAN PAN</h4>
                    </div>
                    <ul className="brief_info_items">
                        <li className="item">
                            <p>成功大學</p>
                            <p>主修:地球科學</p>
                            <p>雙主修:工業與資訊管理</p>
                            <hr/>
                        </li>
                        <li className="item">
                            <p>專長:</p>
                            <p>網頁設計、程式設計、專案管理、文書處理</p>
                            <hr/>
                        </li>
                        <li className="item">
                            <p>程式技能:</p>
                            <p>HTML／CSS／JavaScript/React
                                C++／MySQL／Python／VBA
                            </p>
                            <hr/>
                        </li>
                        <li className="item">
                            <p>設計技能:</p>
                            <p>Adobe XD</p>
                            <p>Adobe premiere</p>
                            <p>Adobe Illustrator</p>
                        </li>
                    </ul>
                    <div className="hello">
                        <p>你好！我是潘小璇！</p>
                        <p>我現在是大四的學生，主修地科系，並雙主修工業與資訊管理系。
                            我從大一開始發覺自己對於簡報排版非常興趣，所以在課堂報上我都喜歡當統整簡報的人。直到接觸網頁設計，學會用程式碼將畫面和功能呈現出來後，就萌生了我對前端設計的嚮往，而我為了更加了解這個領域，也累積了許多課外自學的經驗!
                        </p>
                    </div>
                </div>
            </div>
        </div>
  )
}
