import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import photo from './images/1.jpg'


export default function Officer() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='officer'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <div>社團與系學會幹部</div>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>擔任日期:大二到大三</p></li>
                                    <li><p>地科系系學會-活動</p></li>
                                    <li><p>流行音樂歌唱社-美宣</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>我在大學期間分別在系學會和社團都有擔任幹部的經驗，以下就簡單分析一下這些工作經驗讓我學習到的能力！</h4>
                                <h4>
                                    <span>溝通能力:</span>美宣需要和大家討論圖片的內容，還有希望的風格等;活動的話除了共同討論以外，需要更主動的提供想法，以及詳細得規劃活動流程
                                </h4>
                                <h4>
                                    <span>製圖能力:</span>能夠利用不同的軟體製作宣傳圖片，了解圖片的設計還有相片調色等能力
                                </h4>
                                <h4>
                                    <span>解決問題與獨立思考:</span>在開會中能提出有幫助的建議來解決問題，同時也會多方考量大家的想法。另外則是課業學習的方面，也有許多獨立學習的經驗
                                </h4>
                                <h4>我也參加了很多系上的活動像是地科營跟地科系的系展，從小隊員慢慢成長到組代的過程中，從學習知識漸漸地轉變成能夠交給別人知識的角色，讓我的大學生活有更不一樣的經驗！</h4>
                                <h5>#地科系展合照-我是中間桌子的中間那個</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery" data-src={photo}>
                                        <div className="img" style={{backgroundImage: `url(${photo})`}} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
