import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import "@fancyapps/ui/dist/fancybox.css";

export default function Ai() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='dataStructure'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <a href="https://github.com/Xiaoxuan0117/data_structure_exercise" target="_blank" rel="noreferrer">資料結構<span>link to github</span></a>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>製作日期:2020年10-12月</p></li>
                                    <li><p>課程:資料結構</p></li>
                                    <li><p>類型:個人作業</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>這是一堂大學期間蠻重要的必修課，內容就是基礎的資料結構與演算法，也因為有這堂課所以才有辦法整理出一些比較完整的作品集。
                                </h4>
                                <h4>以下就是一些課程的作業，主要內容是數據的排序和簡單運算功能。<span>每個都有在GitHub上</span>，所以也可以下載來試試看！</h4>
                                <h3 className='mt-3'>
                                    1. 計算總和與比大小
                                </h3>
                                <h4>
                                    <span>程式功能</span>
                                    <ul>
                                        <li>-輸出數據總和</li>
                                        <li>-輸出最大、第二大，最小、第二小的數字</li>
                                        <li>-輸出乘積</li>
                                    </ul>
                                </h4>
                                <h3 className='mt-4'>
                                    2. 隨機數學題
                                </h3>
                                <h4>
                                    <span>程式功能</span><br/>
                                    隨機生成數學題，使用者回答完後會後輸出回答結果和修正
                                </h4>
                                <h3 className='mt-4'>
                                    3. 插入排序法(insertion sort)與合併排序法(merge sort)
                                </h3>
                                <h4>
                                    <span>程式功能</span><br/>
                                    利用兩種排序法進行數據排序
                                </h4>
                                <h3 className='mt-4'>
                                    4. 二進位轉換
                                </h3>
                                <h4>
                                    <span>程式功能</span><br/>
                                    讀取數據資料後將裡面的數字轉換成二進位
                                </h4>
                                <h3 className='mt-4'>
                                    5. 堆排法(heap sort)與快速排序法(quick sort)
                                </h3>
                                <h4>
                                    <span>程式功能</span><br/>
                                    利用兩種排序法進行數據排序
                                </h4>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
