import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import AOS from "aos";
import "aos/dist/aos.css";


export default function Header() {
    useEffect(() => {
        AOS.init();
        AOS.refresh();
      }, []);

    return (
        <div>
            <div className="head">
                <div className="imgbox">
                    <div className="image_inner">
                        <div className="image"></div>
                    </div>
                </div>
                <div data-aos="fade-up" className='head_title_box'>
                    <Link className="head_title color-white u-clearfix" to="/">
                        <h1 className="head_title_text" >XIAO XUAN</h1>
                        <img className="head_img" src={require('../images/head_img.png')} alt="" />
                        <h1 className="head_title_text" >PAN</h1>
                    </Link>
                </div>
                <div className="head_scroll">
                    <h4 className="head_scroll_text color-white">SCROLL</h4>
                    <img src={require('../images/scroll_arrow.png')} alt=""/>
                </div>
            </div>
        </div>
    )
}
