import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import '@fancyapps/ui'
import "@fancyapps/ui/dist/fancybox.css";
import Overview from './images/overview.png'
import testForm1 from './images/testForm1.PNG' 
import testForm2 from './images/testForm2.PNG'
import Poster1 from './images/poster1.png'
import Poster2 from './images/poster2.png'


export default function ProjectManagement() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='projectManagement'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <a href="https://reurl.cc/k7zZWq" target="_blank" rel="noreferrer" style={{pointerEvents:'none'}}>專案管理系統</a>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>製作日期:2021年3-12月</p></li>
                                    <li><p>課程:工資管專題</p></li>
                                    <li><p>類型:小組專題</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>這是我大學期間一個比較大型的專題，主題是<span>為接單式生產的中小型企業客製化一個專案管理系統</span>。
                                </h4>
                                <h4>我們找到的教授通常給學生的題目是比較實務類型的，所以幫我們找到了一間在安南附近製造塑膠製品機台的工廠，讓我們可以<span>透過幫這間工廠改善工作流程來學習專案管理</span>。
                                </h4>
                                <h4>期間我們往返工廠多次，和老闆以及設計師討論工作會遇到的任務以及內容，然後依據收集到的資訊去製作管理系統。
                                </h4>
                                <h4>我們是運用<span>Microsoft Project</span>這個軟體來寫Visual Basic Application，去加上我們需要的功能，因此完成專題後也學會用Microsoft Excel做更進階的程式功能！
                                </h4>
                                <h4>接下來將介紹系統架構和我負責的部分。
                                </h4>
                                <h3 className='mt-4'>1. 整體架構</h3>
                                <h4>首先就是我們整個系統的整體架構，我們利用<span>在功能標籤裡面增加客製化欄位</span>的方法來加入功能，像圖中展示的，上面有兩個屬於"專案計畫"、"專案控制"的標籤，然後標籤內就有我們做的功能表單，分成了<span>專案計畫</span>、<span>回報進度</span>、<span>查看圖表</span>、<span>查看報表</span>和<span>提醒與警告</span>這些項目，可以讓工廠在製作機台的過程中去更新以及追蹤製作的進度和成果。那我負責的是進度中的<span>測試</span>這個項目，還有<span>專題海報</span>的部分。
                                </h4>
                                <h5>#系統首頁</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery1" data-src={Overview}>
                                        <div className="img" style={{backgroundImage: `url(${Overview})`}} />
                                    </div>
                                </div>
                                <h3 className='mt-4'>2. 測試表單</h3>
                                <h4>這裡會有測試的工作階段是因為我們合作的工廠主要工作就是製造大型機台，像我們這次專題所製作的系統就是以一種叫做<span>吹待機</span>的機器為對象來規劃管理系統，那在機台交付給客戶之前就要先進行測試，測試結果通過之後才會進入下一階段。
                                </h4>
                                <h4>測試最重要的就是<span>測試的日期以及測試的報告</span>。參考下圖，使用者可以透過此表單看到<span>未完成與已完成的任務</span>，然後點選未完成的任務可以為該任務<span>輸入日期進度。</span>
                                </h4>
                                <h5>#測試表單:任務進度更新表</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery2" data-src={testForm1}>
                                        <div className="img" style={{backgroundImage: `url(${testForm1})`}} />
                                    </div>
                                </div>
                                <h4>另外測試的地方還需要可以建立測試報告，因此表單的另一個頁面就可以為已完成的測試去建立<span>Excel檔案</span>的報告還有<span>輸出成PDF檔案</span>
                                </h4>
                                <h5>#測試表單:新增測試報告</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery3" data-src={testForm2}>
                                        <div className="img" style={{backgroundImage: `url(${testForm2})`}} />
                                    </div>
                                </div>
                                <h3 className='mt-4'>3. 專題海報</h3>
                                <h4>最後我們就把製作好的系統放到公司那裡，然後製作說明影片以及遠端控制來處理對方使用上遇到的問題，接下來是海報的部分，針對本次專題內容有大致的介紹</h4>
                                <Carousel className='myCarousel'>
                                    <div className="myCarouselObj" data-fancybox="gallery4" data-src={Poster1}>
                                        <img src={Poster1} alt=""/>
                                    </div>
                                    <div className="myCarouselObj" data-fancybox="gallery4" data-src={Poster2}>
                                        <img src={Poster2} alt=""/>
                                    </div>
                                </Carousel>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
