import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'


export default function Marketing() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='markrting'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <a href="https://youtu.be/WUxE_4HSaCU" target="_blank" rel="noreferrer">數位行銷設計師<span>link to youtube</span></a>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>製作日期:2020年7-8月</p></li>
                                    <li><p>暑期實習-程式驅動</p></li>
                                    <li><p>教育部青年暑工計畫</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>這是我在大學期間比較正式的一個實習經驗，我透過教育部的青年暑工計畫到了<span>程式驅動協會</span>擔任<span>數位行銷設計師</span>，我與夥伴在兩個月的實習期間製作了10部資訊科技相關的影片，主要影片內容有趣味的短劇和專家訪問，而<span>我是負責的部分是專家訪問的剪輯</span>，所以我經過這次的經驗也學會了如何使用<span>Premiere</span>！
                                </h4>
                                <h4>這是其中一部影片，點擊標題也可以連到youtube查看更多內容!</h4>
                            </div>
                            <iframe　className='video' src="https://www.youtube.com/embed/hCW6LHsRIz8" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
