    const [scrollHeight, setScrollHeight] = useState(1000)
    const [classShow, setClassShow] = useState(0)

    useEffect(()=>{
        //set aos
        AOS.init();
        AOS.refresh();

        let newScrollHeight = document.getElementById('wrapper').scrollHeight - window.innerHeight
        setScrollHeight(newScrollHeight)

        window.addEventListener("resize", adjustScrollLine)

        function adjustScrollLine(){
            //adjust scrollLine
            let newScrollHeight = document.getElementById('wrapper').scrollHeight - window.innerHeight
            setScrollHeight(newScrollHeight)
        }

        // show header when scroll
        window.addEventListener("scroll",showHeader);

        function showHeader(){
            var windowScrollTop = window.scrollY;
            var header = document.getElementById('header')
            if(header){
                if (windowScrollTop > 80){
                    header.classList.add("header_bg");
                }
                else{
                    header.classList.remove("header_bg");
                }
            }
        }

        return function cleanup(){
            window.removeEventListener('scroll', showHeader)
            window.removeEventListener('resize', adjustScrollLine)
        }
    },[scrollHeight])

    useEffect(()=>{
        // show side-navbar
        var headerSpan = document.getElementById('header_span')
        let sideOut = 0;
        headerSpan.addEventListener('click', ()=>{
            var sideNav = document.querySelector('.navbar');
            var spanTop = document.getElementById('span_top');
            var spanMd = document.getElementById('span_md');
            var spanBottom = document.getElementById('span_bottom');
            if(sideOut === 0){
                sideNav.classList.remove("hide_nav_bar");
                sideNav.classList.add("side_nav_box_shadow");
                spanTop.classList.add("span_top");
                spanMd.classList.add("span_md");
                spanBottom.classList.add("span_bottom");
                sideOut = 1;
            }
            else{
                sideNav.classList.add("hide_nav_bar");
                sideNav.classList.remove("side_nav_box_shadow");
                spanTop.classList.remove("span_top");
                spanMd.classList.remove("span_md");
                spanBottom.classList.remove("span_bottom");
                sideOut = 0;
            }
        })
    }, [])

