import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import "@fancyapps/ui/dist/fancybox.css";

export default function OceanPollution() {
    
    //import images
    function importAll(r) {
        let images = [];
        r.keys().map((item,index) => {
            return images[index] = r(item); 
        });
        return images
    }
    const images = importAll(require.context('./images', false, /\.PNG/));
    
    useEffect(()=>{

        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='oceanPollution'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <div>海洋汙染</div>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>製作日期:2019年11-12月</p></li>
                                    <li><p>課程:海洋學</p></li>
                                    <li><p>類型:小組報告</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>海洋學上的內容就是跟海洋有關的化學反應，還有海底的一些地形，印象深刻的內容有<span>海底火山</span>、<span>可燃冰</span>、<span>同位素</span>等。
                                </h4>
                                <h4>不過這邊的重點是想分享我們在期中的時候做了一個報告，然後我是負責統整簡報的部分</h4>
                                <h4>接下來就是簡報內容的紀錄。
                                </h4>
                                <Carousel className='myCarousel'>
                                    {
                                        images.map((imageObj,index)=>{
                                            return(
                                                <div key={index} className="myCarouselObj" data-fancybox="gallery" data-src={imageObj}>
                                                    <img src={imageObj} alt=""/>
                                                </div>
                                            )
                                        })
                                    }
                                </Carousel>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
